<template>
  <div>
    <h2>我是About组件</h2>
  </div>
</template>

<script>
export default {
  name:'About',
  // beforeDestroy(){
  //   console.log('About组件即将被销毁');
  // },
  mounted() {
  },
  //通过路由规则，进入该组件时被调用
  beforeRouteEnter(to,from,next){
    console.log('app--beforeRouteEnter')
    if(to.meta.isAuth){
      if(localStorage.getItem("about") === '关于'){
        next()
      }
      else
      alert("您没有权限")
    }
  },
  // 通过路由规则，进入该组件时被调用
  beforeRouteLeave(to,from,next){
    console.log('app--beforeRouteLeave',to,from)
    next()
  }
}
</script>

